@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  -webkit-justify-content: center;

          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  height: 100%;
}

.config {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  position: fixed;

  top: 2rem;
  left: 2rem;

  width: 15rem;
  height: auto;
}

.counters {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  width: 15rem;

  padding: 1rem;

  border: 1px solid rgb(184, 184, 184);
  border-radius: 0.4rem;
}

.counters {
  height: 5rem;

  margin-bottom: 1rem;
}

.text-input,
.text-progression {
  font-family: 'Roboto Mono', monospace;
}

.text-input {
  all: unset;

  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  color: transparent;
}

.text-input:focus {
  border-color: rgb(55, 131, 231);
}

.text-progression {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;

  width: 100%;
  padding: 1rem 1.5rem;

  margin-bottom: 3rem;

  color: rgb(78, 78, 78);
  border: 1px solid #cccccc;

  -webkit-filter: grayscale(0.3) opacity(0.5);

          filter: grayscale(0.3) opacity(0.5);

  transition: all 0.2s ease-in-out;
}

.text-progression.active {
  -webkit-filter: none;
          filter: none;
}

.text-progression .key {
  color: black;
  opacity: 0.5;
  pointer-events: none;
}

.text-progression .key.valid,
.text-progression .key.invalid {
  opacity: 1;
}

.text-progression .key.valid {
  color: #1ece35;
}

.text-progression .key.invalid {
  color: #d62828;
}

.keyboard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  margin-top: 1rem;
}

.row {
  --size: 3.5rem;
  --gap: 1rem;
  --offset: 0.3rem;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.row .key {
  z-index: 10;
  position: relative;

  width: var(--size);
  height: var(--size);

  -webkit-flex-shrink: 0;

          flex-shrink: 0;

  cursor: pointer;
}

.row .key::before,
.row .key::after {
  content: '';

  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  border-radius: 0.05rem;
}

.row .key::before {
  z-index: 7;
  top: var(--offset);

  background: #bdbdbd;
  transition: background 0.2s ease-in-out;
}

.row .key.disabled::before {
  background: #d4d4d4;
}

.row .key::after {
  z-index: 8;

  background: linear-gradient(104.74deg, #cccccc -47.49%, #e3e3e3 111.91%);

  transition: top 50ms ease-in-out, background 0.2s ease-in-out;
}

.row .key.disabled::after {
  background: linear-gradient(104.74deg, #d4d4d4 -47.49%, #ebebeb 111.91%);
}

.row .key.down::after {
  top: var(--offset);
}

.row:not(:first-child) .key {
  margin-top: var(--gap);
}

.row .key:not(:last-child) {
  margin-right: var(--gap);
}

.row .key .content {
  z-index: 10;

  position: absolute;
  top: 50%;
  left: 50%;

  color: black;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;

  -webkit-transform: translate(-50%, -50%);

          transform: translate(-50%, -50%);

  transition: top 50ms ease-in-out, background 0.2s ease-in-out;
}

.row .key.disabled .content {
  color: rgb(165, 165, 165);
}

.row .key.down .content {
  top: calc(50% + var(--offset));
}

.row .key .content {
  overflow: hidden;
  text-overflow: '';
  white-space: nowrap;

  width: calc(100% - var(--size) * 0.5);

  text-align: center;
}

.row .key:is(.tab, .caps-lock, .l-shift, .r-shift, .ctrl, .alt, .alt-gr) {
  width: calc(var(--size) * var(--proportion));
}

.row .key:is(.tab, .caps-lock, .l-shift, .r-shift, .ctrl, .alt, .alt-gr) .content {
  text-align: left;
}

.row .key.caps-lock {
  --proportion: 1.8;
}

.row .key.l-shift {
  --proportion: 1.2;
}

.row .key.r-shift {
  --proportion: 2.8;
}

.row .key.ctrl {
  --proportion: 1.3;
}

.row .key.alt {
  --proportion: 1.3;
}

.row .key.alt-gr {
  --proportion: 1.4;
}

.row .key.space,
.row .key.del,
.row .key.enter,
.row .key.tab {
  width: 100%;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
}

.row .key.enter-bottom {
  height: calc(100%);

  margin-top: 0;
}

